.project-tile {
	height : 100%;
	min-height: 60px;
	position: relative;
	overflow: hidden;
	aspect-ratio: 1;
	
	&__back, .back {
		width: 100%;
		height: 100%;
		position: absolute;
		inset: 0;

		&__inner {
			width: 100%;
			height: 100%;
		}

		&__image {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

	}
	
	&__front, .front {
		width: 100%;
		height: 100%;
		position: absolute;
		transition: all 0.6s ease;
		inset: 100% 0 0 0;
		background-color: $white;
		
		&--white { background-color: $white; }
		&--black { background-color: $black; }

		&__inner {
			padding: 24px;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
		}

		&__title {
			margin-top: 48px;
			margin-bottom: 8px;
		}

		&__link {
			align-self: flex-end;
			margin-top: auto;
			display: block;
		}

		&__title, 
		&__subtitle, 
		&__link {
			opacity: 0;
			position: relative;
			top: 16px;
			transition: all 0.6s ease;
		}
		
	}

	&:hover {
		.project-tile__front { inset: 0;  }
		.front__title, 
		.front__subtitle, 
		.front__link {
			opacity: 1;
			top: 0px;
			transition-delay: 0.3s;
		}	
		
	}

}
.service-tile {
	height : 100%;
	min-height: 60px;
	position: relative;
	overflow: hidden;

	&__image {
		width: 100%;
		height: 100%;
		inset: 0;
		background-size: cover;
		object-fit: cover;
		aspect-ratio: 1;
		display: block;
	}

	&__label, .label {
		width: 100%;
		padding: 16px;
		position: absolute;
		bottom: 0;
		background-color: $white;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;

		&__title {
			color: black;
			margin-top:4px;
			line-height: 1.5;
			margin-right: 16px;
			transition: all 0.15s ease
		}

		&__icon {
			color: $black;
			transform: rotate(270deg);
		}
	
	}

	&:hover {
		
		.label__title {
			margin-right: 24px;
		}
		
	}

}
.teaser {

	&__heading {
		margin-bottom: 24px;
		max-width: 440px;
	}

	&__line {
		margin-bottom: 32px;
	}

	&__text {
		margin-bottom: 48px;
		max-width: 880px;
	}

}
.team-tile {
	height : 100%;
	min-height: 60px;
	position: relative;
	overflow: hidden;
	background-color: $blue;
	
	&:nth-child(2) {
		background-color: darken($blue, 10%)
	}

	&:hover {

		.team-tile__square {
			transform: translate(-50%, -50%) rotate(360deg);
			height: 400%;
			width: 400%;
		}
		
	}
	
	&__square {
		width: 50px;
		height: 50px;
		background-color: $orange;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transition: all 3s ease;
	}
	
	&__image {
		width: 100%;
		height: 100%;
		inset: 0;
		background-size: cover;
		object-fit: cover;
		aspect-ratio: 1;
		display: block;
		position: relative;
	}

	&__label, .label {
		width: 100%;
		padding: 16px;
		position: absolute;
		bottom: 0;
		background-color: $white;
		display: grid;
		grid-template-areas:
			"name icon"
			"role icon";
		grid-template-columns: auto 40px;

		&__name {
			grid-area: name;
			color: black;
			margin-top:4px;
			line-height: 1.5;
			margin-right: 16px;
		}

		&__role {
			grid-area: role;
			color: $black;
			font-size: 12px;
		}

		&__icon {
			grid-area: icon;
			color: $black;
			transform: rotate(0deg);
			font-size: 32px;
			align-self: center;
		}
	
	}

	&:hover {
		
		.label__title {
			margin-right: 24px;
		}
		
	}

}
.cookie-banner {
	position: fixed;
	top: 100%;
	width: 100%;
	background-color: $white;	
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 16px;
	transition: all 1s ease;

	&--visible {
		transform: translateY(-100%);
	}

	&__text, &__link {
		color: $black;
	}

	&__link {
		text-decoration: underline;
	}

	&__button {
		background-color: $black;
		padding: 12px;
		border-radius: 6px;
		cursor: pointer;
		margin-left: 12px;
	}

@media (max-width: 550px) {
	flex-direction: column;



	&__text {
		margin-bottom: 16px;
	}

	&__button {
		width: 100%;
		margin-left: 0;
	}

}

}

.navigation-mobile {
	box-shadow: $box-shadow;
	display: none;
	position: relative;

	&__inner {
		background-color: $black;
	}
	
	&__bar {
		display: flex;
		flex-direction: row;
